import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service'
declare var $: any;
import 'jquery';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  posts = [];
  comments = []
  photos = [];
  users = [];
  commentsNum = 0;
  imgPath = " ";



  constructor(private _api: ApiService) {
    _api.getPHOTOS().subscribe(data => {
      this.photos = data;
      console.log(data)

    });


    _api.getPOSTS().subscribe(data => this.posts = data);

    _api.getCOMMENTS().subscribe(data => { this.comments = data });
    _api.getUSERS().subscribe(data => this.users = data);




    for (var i = 0; i < this.posts.length; i++) {
      if (this.posts[i].id == undefined) {
        this.posts[i].id = 0;



      }
    }


    for (var i = 0; i < this.photos.length; i++) {
      if (this.photos[i].thumbnailUrl == undefined && this.photos[i].url) {
        this.photos[i].thumbnailUrl = " ";
        this.photos[i].url = " ";



      }
    }

  }



  countComments() {

  }
  ngOnInit() {

    
    for (var i = 0; i < this.photos.length; i++) {
      if (this.photos[i].thumbnailUrl == undefined && this.photos[i].url == undefined) {
        this.photos[i].thumbnailUrl = " ";
        this.photos[i].url = " ";
      }

    }

    $(document).ready(function () {
      $(".owl-carousel").owlCarousel({
        // autoWidth:true
        items: 10,
        nav: true,
        navText: ['<i class="fa fa-angle-left" aria-hidden="true"></i>', '<i class="fa fa-angle-right" aria-hidden="true"></i>']
      });
    });




    for (var i = 0; i < this.posts.length; i++) {
      if (this.posts[i].id == undefined && this.posts[i].body == undefined) {
        this.posts[i].id = 0;
        this.posts[i].body = ' ';



      }
    }


    for (var i = 0; i < this.users.length; i++) {
      if (this.users[i].id == undefined && this.users[i].name == undefined) {
        this.posts[i].id = 0;
        this.users[i].name = " ";




      }
    }


  }
}

